    max-width: 100% !important;
  }
  
  .calendar-grid {
    width: 100% !important;
    max-width: 100% !important;
  }
  
  .calendar-days {
    width: 100% !important;
    max-width: 100% !important;
  }
  
  /* Ensure fixed width on small screens */
  .calendar-day {
    width: 100%;
    max-width: 100%;
    min-width: 0;
  }
}

/* Hide day-header-small on large screens */
@media (min-width: 1400px) {
  .day-header-small {
    display: none !important;
  }
  
  /* Hide day-events on large screens in day view */
  .calendar-day.day-view-day .day-events {
    display: none !important;
  }
}

/* Small screens - mobile devices */
@media (max-width: 991px) {
  .calendar-days {
    grid-template-columns: repeat(7, 1fr);
    width: 100%;
    gap: 1px;
  }
  
  .calendar-day {
    width: 100%;
    max-width: 100%;
    min-width: 0;
    min-height: 80px;
    padding: 4px;
  }
  
  .day-number {
    width: 24px;
    height: 24px;
    font-size: 0.8rem;
  }
  
  .event-bullet {
    width: 8px;
    height: 8px;
  }
  
  /* Day button styling for small screens - full width */
  .day-header-small {
    text-align: center;
    padding: 4px 2px;
    width: 100%;
  }
  
  .day-header-small .day-number {
    display: inline-block;
    width: 28px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    border-radius: 50%;
    background: #f8f9fa;
    border: 2px solid #e9ecef;
    font-weight: 600;
    font-size: 0.8rem;
    color: #2c3e50;
  }
}

/* Responsive Day View */
@media (max-width: 768px) {
  .day-view-content {
    padding: 20px;
    gap: 20px;
  }
  
  .day-name {
    font-size: 2rem;
  }
  
  .day-date {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 1.3rem;
  }
  
  .event-item {
    padding: 15px;
    gap: 12px;
  }
  
  .event-title {
    font-size: 1rem;
  }
  
  .calendar-day {
    min-height: 60px;
    padding: 2px;
  }
}

/* Medium screens - slightly larger event bullets */
@media (min-width: 992px) and (max-width: 1399px) {
  .event-bullet {
    width: 18px;
    height: 18px;
  }
  
  /* Day button styling for medium screens - full width */
  .day-header-small {
    text-align: center;
    padding: 6px 3px;
    width: 100%;
  }
  
  .day-header-small .day-number {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 50%;
    background: #f8f9fa;
    border: 2px solid #e9ecef;
    font-weight: 600;
    font-size: 0.85rem;
    color: #2c3e50;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .calendar-container {
    padding: 10px;
  }
  
  .calendar-header {
    flex-direction: column;
    gap: 20px;
    align-items: stretch;
  }
  
  .header-right {
    flex-direction: column;
    gap: 15px;
  }
  
  .calendar-filters {
    flex-direction: column;
    gap: 15px;
  }
  
  .summary-card {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .calendar-days {
    min-height: 60px;
  }
  
  .calendar-day {
    min-height: 60px;
    padding: 4px;
  }
  
  .day-events {
    max-height: 30px;
    gap: 2px;
  }
  
  .event-bullet {
    width: 12px;
    height: 12px;
  }
  
  .more-events-indicator {
    font-size: 8px;
    padding: 1px 3px;
  }
  
  .modal-content {
    margin: 5px;
    max-height: 95vh;
    width: 98%;
    max-width: none;
  }
  
  .event-meta {
    flex-direction: column;
    gap: 8px;
  }
  
  .day-events-modal .modal-content {
    margin: 5px;
    max-height: 95vh;
    width: 98%;
    max-width: none;
  }
  
  .day-events-modal .modal-header {
    padding: 15px;
  }
  
  .day-events-modal .modal-header h3 {
    font-size: 1.3rem;
  }
  
  .day-events-modal .modal-body {
    padding: 15px;
  }
  
  .day-events-modal .event-meta {
    flex-direction: column;
    gap: 8px;
  }
}

/* Extra small screens */
@media (max-width: 480px) {
  .calendar-container {
    padding: 5px;
  }
  
  .calendar-header {
    padding: 15px;
  }
  
  .calendar-header h1 {
    font-size: 1.5rem;
  }
  
  .calendar-filters {
    padding: 15px;
  }
  
  .summary-card {
    padding: 15px;
  }
  
  .calendar-days {
    min-height: 50px;
  }
  
  .calendar-day {
    min-height: 50px;
    padding: 3px;
  }
  
  .day-number {
    font-size: 14px;
  }
  
  .day-events {
    max-height: 25px;
    gap: 1px;
  }
  
  .event-bullet {
    width: 10px;
    height: 10px;
  }
  
  .more-events-indicator {
    font-size: 7px;
    padding: 1px 2px;
  }
  
  .week-day {
    padding: 10px 5px;
    font-size: 12px;
  }
}

/* Very small screens - enable horizontal scrolling */
@media (max-width: 360px) {
  .calendar-grid {
    overflow-x: auto;
    min-width: 320px;
  }
  
  .week-days,
  .calendar-days {
    min-width: 320px;
  }
  
  .week-day,
  .calendar-day {
    min-width: 45px;
  }
}
